<template>
  <div class="cms-list-layout">
    <cms-tabs>
      <cms-tab to="/shopAccount/accountList/list">账户列表</cms-tab>
      <cms-tab to="/shopAccount/submitRecord/list">提现记录</cms-tab>
    </cms-tabs>
    <cms-prompt collapse='true'>
      <li>店铺的提现申请及历史记录</li>
      <li>可对提现申请进行审核、删除操作</li>
    </cms-prompt>
    <!-- 筛选区 -->
    <section class="cms-list-query">
      <div class="query-group">
        <label>店铺名称</label>
        <el-input v-model="queryParams.storeName" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <label>状态</label>
        <el-select v-model="queryParams.status"  @change="query" >
          <el-option label="全部" value=""></el-option>
          <el-option label="待审核" :value="1"></el-option>
          <el-option label="审核不通过" :value="2"></el-option>
          <el-option label="待转账" :value="3"></el-option>
          <el-option label="转账失败" :value="4"></el-option>
          <el-option label="完成" :value="5"></el-option>
        </el-select>
      </div>
      <div class="query-group">
      <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
      </div>
    </section>

    <el-table :data="tableData" style="width: 100%" stripe v-loading="loading" @selection-change='getCheckItems' @row-click='checkRows' ref='dataTable'>
      <el-table-column type="selection" width="46" fixed='left'></el-table-column>
      <el-table-column prop="presentCode" label="提现编号" min-width="200"></el-table-column>
      <el-table-column prop="storeName" label="店铺名称" min-width="160" align="center"></el-table-column>
      <el-table-column prop="accountTypeCode" label="账户类型" min-width="130" align="center">
        <template slot-scope="scope">
          <span v-if="scope.row.accountTypeCode == '1'">支付宝</span>
          <span v-else-if="scope.row.accountTypeCode == '2'">微信</span>
          <span v-else-if="scope.row.accountTypeCode == '3'">银联</span>
          <span v-else-if="scope.row.accountTypeCode == '4'">余额</span>
        </template>
      </el-table-column>
      <el-table-column prop="storeUsername" label="账户" min-width="150" align="center"></el-table-column>
      <el-table-column prop="money" label="提现金额" min-width="100" align="center"></el-table-column>
      <el-table-column prop="tradeNo" label="交易流水号" min-width="300" align="center"></el-table-column>
      <el-table-column prop="status" label="状态" min-width="100" align="center">
        <div slot-scope="scope">
          <span class="info">{{statusFilter(scope.row.status)}}</span>
        </div>
      </el-table-column>
      <el-table-column prop="createTime" label="申请时间" min-width="170" align="center">
        <template slot-scope="scope">
          {{$moment(scope.row.createTime)}}
        </template>
      </el-table-column>
      <el-table-column prop="finshTime" label="完成转账时间" min-width="170" align="center">
        <template slot-scope="scope">
          {{$moment(scope.row.finshTime)}}
        </template>
      </el-table-column>
      <el-table-column label="操作选项" width='210' fixed="right" align="right">
        <div slot-scope="scope">
          <cms-button  icon='edit' size='small' type='warning' v-if="scope.row.status === 1" @click="examine(scope.row.id)" >审核</cms-button>
          <cms-button  icon='error' size='small' type='info' v-if="scope.row.status === 1" @click="examineDis(scope.row.id)" >审核不通过</cms-button>
          <cms-button v-if="scope.row.status === 4" icon='error' size='small' type='info' @click="deleteData(scope.row.id)">删除</cms-button>
        </div>
      </el-table-column>
    </el-table>
    <cms-pagination @change="getPage" :total='total'></cms-pagination>
  </div>
</template>
<script>
import list from '@/mixins/list'
import {
  stoPrePage,
  stoPreDelete,
  stoPreAudit
} from '@/api/shop/shopAccount.js'
var defaultData = {
  'storeName': '',
  'storeUsername': '',
  'status': '',
  'page': 1,
  'size': 10
}
export default {
  mixins: [list],
  data() {
    return {
      queryParams: Object.assign({}, defaultData)
    }
  },
  methods: {
    statusFilter(val) {
      var arr = ['', '待审核', '审核不通过', '待转账', '转账失败', '完成']
      return arr[parseInt(val)]
    },
    examine(id) {
      this.$confirm(
        '提现申请通过后，系统将自动调用支付插件中设置的支付宝账户进行转账，请谨慎操作',
        '系统提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }
      )
        .then(() => {
          stoPreAudit({ ids: [id] }).then(res => {
            if (res.code === 200) {
              this.$successMessage('审核成功')
              this.getTableData(stoPrePage, this.queryParams)
            } else {
              this.$errorMessage(res.message)
            }
          })
        })
        .catch(() => {})
    },
    examineDis(id) {
      this.$prompt('原因', '系统提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        inputPlaceholder: '请输入100字以内不通过原因',
        inputType: 'textarea',
        inputPattern: /^.{1,100}$/,
        inputErrorMessage: '请输入100字以内不通过原因'
      }).then(({ value }) => {
        stoPreAudit({ ids: [id], data: value }).then(res => {
          if (res.code === 200) {
            this.$successMessage('审核成功')
            this.getTableData(stoPrePage, this.queryParams)
          } else {
            this.$errorMessage(res.message)
          }
        })
      }).catch(() => {})
    },
    deleteData(id) { // 包装一层删除的操作
      this.deleteTableData(stoPreDelete, { ids: [id] }) // 这个才是真实的删除方法
    },
    getTableData(url, params, isPage = true) {
      // 获取表格数据,处理翻页选中
      this.tableData = []
      this.loading = true
      this.callback = url
      this.isPage = isPage
      setTimeout(() => {
        url(params)
          .then(res => {
            if (res.code === 200) {
              if (isPage) {
                this.total = res.data.totalElements
                this.tableData = res.data.content
              } else {
                this.tableData = res.data.content
              }
            } else {
              this.$errorMessage(res.message)
            }
            this.loading = false
          })
          .catch(res => {
            this.loading = false
          })
      }, 1)
    }
  },
  created() {
    // 首次触发
    this.getTableData(stoPrePage, this.queryParams)
  }
}
</script>

<style>
</style>



